.main-nav {

  margin-right: 85px;

  &__toggle {
  }

  &__toggle_lg-hidden {
    display: none;
  }

  &__list {
    display: flex;
    list-style: none;
    margin: 0;
  }

  &__item {
    margin-right: 20px;
    font-size: $font-size;
    font-weight: $font-weight-bd;
    text-transform: lowercase;
  }

  &__link {
    color: $gray-ultimate;

    &:hover {
      color: $link-color-hover;
    }
  }
}

